.video-js {
  height: 200px;
  width: 355px;
  border-radius: 5px;
  display: block !important;
}

.video-js video {
  border-radius: 5px;
}

.camera-select {
  margin: 10px 0 0 0;
}

.camera-select .mat-mdc-select-trigger {
  background: #eee;
  padding: 5px 10px;
  border-radius: 5px;
  border: 1px solid #666;
  text-align: center;
}

.stream-close {
  float: right;
  margin-left: 20px;
  border: 1px solid #666;
  width: 25px;
  height: 25px;
  border-radius: 15px;
  cursor: pointer;
  line-height: 22px;
}

.mat-mdc-option .mat-pseudo-checkbox-minimal {
  position: absolute;
  right: 16px;
}

.mat-mdc-option .mdc-list-item__primary-text {
  width: 100%;
  text-align: center;
}

.stream-loader {
  margin: 0 3px 0 15px;
  float: right;
  width: 20px !important;
  height: 20px !important;
}

.mat-mdc-card-header-text {
  width: 100%;
  margin-bottom: 10px;
}

.mat-mdc-card-footer {
  margin-top: 10px;
}

.device-name {
  line-height: 26px;
  font-size: 18px;
  float: left;
}
